import test from 'ava';

// var rewire = require('rewire'); // pour les spies

import * as Client from './client.js';

test('can query for DOM elements', t => {
    document.body.innerHTML = '<p>Hello, world</p>';

    const para = document.querySelector('p');

    t.is(para.innerHTML, 'Hello, world');
});

test.only('afficher / masquer le texte', t => {
  document.body.innerHTML = '<div id="seeMoreDiv" style="display:none">Des explications très intéressantes</div><button id="seeMoreButton" onclick="seeMore()">Voir plus</button>';
  Client.seeMore();
  t.is(document.getElementById('seeMoreDiv').style.display, 'none');
});
